import React, { Component } from 'react'
import SecondAPI from '../../Component/SecondAPI/SecondAPI';
import pimg from "../Home/images/Group 7259.svg"
import PNB from '../Home/images/PNB Housing Finance Ltd/1.png'
import PNBF1 from '../Home/images/PNB Housing Finance Ltd/Safety Assurance.svg'
import PNBF2 from '../Home/images/PNB Housing Finance Ltd/Interest Rate.svg'
import PNBF3 from '../Home/images/PNB Housing Finance Ltd/Premature Withdrawal.svg'
import Higher from '../Home/images/PNB Housing Finance Ltd/Higher Returns.svg'
import Strong from '../Home/images/PNB Housing Finance Ltd/Strong Financials.svg'
import Flexible from '../Home/images/PNB Housing Finance Ltd/Flexible Payout Options.svg'
import reactHtmlParser from 'react-html-parser';
import { withTranslation } from 'react-i18next';
import {Helmet} from 'react-helmet';


export class BajajFinanceLtd extends Component {
    data = {
        "BajajFinance": {
            "Title": "",
            "ProductCategoryId": "2",
            "SebiCategory": "",
            "SebiSubCategory": "",
            "DefaultProductId": "1",
        }
    }
    render() {
        const productinfo = this.data.BajajFinance;
        const{t} = this.props;

        return (
            <>
            <Helmet>
                <meta charSet="utf-8" />
                <title>{t('MetaTitle:mT61')}</title>
                <meta name="description" content={t('MetaDescription:mD61')} />
            </Helmet>
                <div className='container container-wrapper'>
                    <div className='col-lg-7 col-md-6 col-12 mb-4'>
                        <h1>{t('PNB:pnbFixed1')}</h1>
                        <p>{t('PNB:pnbFixed2')}</p>
                        <a href="https://app.nivesh.com/home/index"><button type="button" className="btn btn-primary btn-lg">{t('CTAFooter:InvestNow1')}</button></a>
                    </div>
                    <div className='col-lg-5 col-md-6'>
                        <img src={PNB} alt="" className='img-fluid' />

                    </div>

                </div>
                <div className="container features ourPartCaptital">
                    <h2>{t('PNB:pnbFixed3')}</h2>
                    <div className='row'>
                        <div className="col-lg-4">
                            <img src={PNBF1} alt="" />
                            <h4>{t('PNB:pnbFixed4')}</h4>
                            <p className='small-text'>{t('PNB:pnbFixed5')}</p>
                        </div>
                        <div className="col-lg-4">
                            <img src={PNBF2} alt="" />
                            <h4>{t('PNB:pnbFixed6')}</h4>
                            <p className='small-text'>{t('PNB:pnbFixed7')}</p>
                        </div>
                        <div className="col-lg-4">
                            <img src={PNBF3} alt="" />
                            <h4>{t('PNB:pnbFixed8')}</h4>
                            <p className='small-text'>{t('PNB:pnbFixed9')}</p>
                        </div>
                    </div>
                    <div className='text-center mt-4'><a href="https://app.nivesh.com/home/index" target="_blank" className='btn btn-primary'>{t('CTAFooter:InvestNow1')}</a></div>
                </div>
                <div className='bg-light ourPartCaptital'>
                    <div className='container container-wrapper'>
                        <h2 className='text-center'>{t('PNB:pnbFixed10')}</h2>
                        <div className='col-lg-4'>
                            <div className='card mt-4'>
                                <div className='card-body'>
                                    <img src={Higher} alt="" />
                                    <h3>{t('PNB:pnbFixed11')}</h3>
                                </div>
                            </div>
                        </div>
                        <div className='col-lg-4'>
                            <div className='card mt-4'>
                                <div className='card-body'>
                                    <img src={Strong} alt="" />
                                    <h3>{t('PNB:pnbFixed12')}</h3>
                                </div>
                            </div>
                        </div>
                        <div className='col-lg-4'>
                            <div className='card mt-4'>
                                <div className='card-body'>
                                    <img src={Flexible} alt="" />
                                    <h3>{t('PNB:pnbFixed13')}</h3>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>

                <div className="API-Section">
               <div className="container container-wrapper">
                  <h2 className='text-center'>{t('PNB:pnbFixed14')}</h2>
                  <div className='row row justify-content-lg-center'>
                     <div className='col col-lg-10'>
                        <SecondAPI productCategoryId={productinfo.ProductCategoryId} sebiCategory={productinfo.SebiCategory} sebiSubCategory={productinfo.SebiSubCategory} defaultProductId={productinfo.DefaultProductId} />
                     </div>
                  </div>
               </div>
            </div>

                <div className='container container-wrapper'>
                    <h2 className='text-center'>{t('PNB:pnbFixed15')}</h2>
                    <div className='col-lg-4 mt-4'>
                        <div className='card'>
                            <div className='card-body'>
                                <img src={pimg} className='check-svg' />
                                <h4>{t('PNB:pnbFixed16')}</h4>
                            </div>
                        </div>
                    </div>
                    <div className='col-lg-4 mt-4'>
                        <div className='card'>
                            <div className='card-body'>
                                <img src={pimg} className='check-svg' />
                                <h4>{t('PNB:pnbFixed17')}</h4>
                            </div>
                        </div>
                    </div>
                    <div className='col-lg-4 mt-4 mb-4'>
                        <div className='card'>
                            <div className='card-body'>
                                <img src={pimg} className='check-svg' />
                                <h4>{t('PNB:pnbFixed18')}</h4>
                            </div>
                        </div>
                    </div>
                    <div className='text-center mt-4'><a href="https://app.nivesh.com/home/index" target="_blank" className='btn btn-primary'>{t('CTAFooter:InvestNow1')}</a></div>
                </div>

                <div className='gardient-red'>
                    <div className='container container-wrapper'>
                        <h2 className='text-white'>{t('PNB:pnbFixed19')}</h2>
                        <p className='text-white'>{t('PNB:pnbFixed20')}</p>
                    </div>
                </div>

                <div className='container container-wrapper'>
                    <h2 className='mt-4'>{t('PNB:pnbFixed21')}</h2>

                    <h3 className='mt-4'>{t('PNB:pnbFixed22')}</h3>
                    <p>{t('PNB:pnbFixed23')}</p>

                    <h3>{t('PNB:pnbFixed24')}
                    </h3>
                    <p>{t('PNB:pnbFixed25')}
                    </p>

                    <h3>{t('PNB:pnbFixed26')}
                    </h3>
                    <p>{t('PNB:pnbFixed27')}
                    </p>

                    <h3>{t('PNB:pnbFixed28')}
                    </h3>
                    <p>{t('PNB:pnbFixed29')}</p>

                    <h3>{t('PNB:pnbFixed30')}</h3>
                    <p>{reactHtmlParser(t('PNB:pnbFixed31'))}</p>

                </div>
            </>
        )
    }
}

export default withTranslation() (BajajFinanceLtd)
